.MainNav {
    display: flex;
    width: 920px;
    align-items: center;
    justify-content: space-around;
    position: absolute;
    left: 50%;
    margin-left: -460px;
}

.HeadNav {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.HeadNav>li {
    float: left;
    display: block;
    color: darkgray;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
.HeadNav>li:hover {
    background-color: #0F0;
}

.FootNav{
    width: 920px;
    position: absolute;
    top: 750px;
    left: 50%;
    margin-left: -460px;    
}
.FootNavHeader{
    width: 100%;
    text-align: center;
}

.FootNavList {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    width: 920px;    
    display: flex;
    align-items: center;
}

.FootNavList>li {
    width:183px;
    float: left;
    display: block;
    color: darkgray;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}
.FootNavList>li:hover{
    background-color: lightblue;
}