.Card_Napr{     
    position: absolute;
    width: 700px;
    height: 600px;
    left: 50%;
    top: 100px;
    margin-left: -350px;     
    overflow-y: scroll;
}
.Card_Napr_list {
    width: 600px;
    left: 50px;
    margin-left: 50px;
    counter-reset: li; 
    list-style: none; 
    font: 14px "Trebuchet MS", "Lucida Sans";
    padding: 0;
    text-shadow: 0 1px 0 rgba(255,255,255,.5);
}
.Card_Napr_list a {
    position: relative;
    display: block;
    padding: .4em .4em .4em 2em;
    margin: .5em 0;
    background: #DAD2CA;
    color: #444;
    text-decoration: none;
    border-radius: .3em;
    transition: .3s ease-out;
}
.Card_Napr_list a:hover {background: #E9E4E0;}
.Card_Napr_list a:hover:before {transform: rotate(360deg);}
.Card_Napr_list a:before {
    content: counter(li);
    counter-increment: li;
    position: absolute;
    left: -1.3em;
    top: 50%;
    margin-top: -1.3em;
    background: #8FD4C1;
    height: 2em;
    width: 2em;
    line-height: 2em;
    border: .3em solid white;
    text-align: center;
    font-weight: bold;
    border-radius: 2em;
    transition: all .3s ease-out;
}